import "./styles.css";
import joto from "../src/joto";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Checkboxes from "./checkboxes";

export default function Joto() {
  return (
    <div className="Joto">
      <Box
        sx={{
          display: "flex",
          flexWrap: "wrap",
          "& > :not(style)": {
            m: 1,
            width: 200,
            height: 150
          }
        }}
      >
        {Array(100)
          .fill(`a`)
          .map((val, i) => {
            return (
              <Paper elevation={3}>
                <ul>
                  <li>{joto[i]["no"]}</li>
                  <li>{joto[i]["name"]}</li>
                  <li>{joto[i]["category"]}</li>
                  <li>{joto[i]["type"]}</li>
                  <Checkboxes />
                </ul>
              </Paper>
            );
          })}
      </Box>
    </div>
  );
}
